import cache from '@/utils/cache/cache'
import { Module } from 'vuex'
import store from '@/store'
import { IRootState } from '../types'
import { ILangState } from './types'
import { vantLocales } from '@/utils/lang'

const langModule: Module<ILangState, IRootState> = {
  state() {
    return {
      language: cache.getCache('language') || 'zhCn'
    }
  },
  // actions: {
  //   async languageAction({ commit }, payload) {}
  // },
  mutations: {
    CHANGE_LANGUAGE(state, value: string) {
      state.language = value
      cache.setCache('language', value)
      // store.dispatch('languageAction')
      vantLocales(value)
    }
  }
}

export default langModule
