
import { useStore } from '@/store'
import { computed, defineComponent, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { PieChart } from '@/components/page-echarts/index'
import NavOrder from '@/components/nav-order/index'
import SellOrder from '@/components/sell-order'
import TopicComment from '../cpns/comment.vue'
import { orderConfig } from '../config/order.config'
import { getPageData, postPageData } from '@/service/main/system/system'
import { i18n } from '@/utils/lang'

export default defineComponent({
  components: {
    PieChart,
    NavOrder,
    SellOrder,
    TopicComment
  },
  setup() {
    const btnDisable = ref(false)
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    const havePaid = ref(false)
    const havePaidPriceYes = ref(0)
    const havePaidPriceNo = ref(0)
    const NavOrderRef = ref<InstanceType<typeof NavOrder>>()
    const SellOrderRef = ref<InstanceType<typeof SellOrder>>()
    const btnText = ref(i18n.global.messages[i18n.global.locale].buy)
    const topic = computed(() => store.state.main.singleTopic)

    const getTopicInfo = async () => {
      await getPageData(`/topic/${route.params.uuid}`).then((res) => {
        store.commit('main/changeSingleTopic', res.data)
      })
      if (store.state.main.singleTopic.is_stop === 1) {
        btnText.value = i18n.global.messages[i18n.global.locale].topicStoped
        btnDisable.value = true
      }
      const end_time = new Date(store.state.main.singleTopic.end_time)
      if (new Date() >= end_time) {
        btnText.value = i18n.global.messages[i18n.global.locale].topicStoped
        btnDisable.value = true
      }
    }
    getTopicInfo()

    const handleBackClick = () => {
      store.commit('main/changeCommentCount', 0)
      store.commit('main/changeCommentList', [])
      router.push('/')
    }

    const checkTopicBuied = async () => {
      if (store.state.login.userInfo.mixin_uuid) {
        const res = await postPageData(`/usertotopic/check`, {
          user_id: store.state.login.userInfo.mixin_uuid,
          tid: route.params.uuid
        })
        if (res.data != null) {
          havePaid.value = true
          const res = await postPageData(`usertotopic/${route.params.uuid}`)
          havePaidPriceYes.value = res.data.yes_ratio_price
          havePaidPriceNo.value = res.data.no_ratio_price
        }
      }
    }
    checkTopicBuied()

    const handleBtnClick = () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction')
      }
      NavOrderRef.value?.handleShowPopup()
      store.dispatch('order/handleOrderAction')
    }

    const handleSellBtnClick = () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction')
      }
      SellOrderRef.value?.ClickSellBtnClick()
    }

    return {
      btnDisable,
      havePaid,
      havePaidPriceYes,
      havePaidPriceNo,
      topic,
      NavOrderRef,
      SellOrderRef,
      orderConfig,
      btnText,
      handleBackClick,
      handleBtnClick,
      handleSellBtnClick
    }
  }
})
