
import { getPageListData, postPageData } from '@/service/main/system/system'
import { useStore } from '@/store'
import { Toast } from 'vant'
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    comment: {
      type: Object,
      required: true
    }
  },
  emits: ['showDlg', 'handlePraiseClick'],
  setup(props, { emit }) {
    const store = useStore()
    const ifPraise = ref(false)

    const CheckIfPraise = async () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction')
        return
      }

      const res = await getPageListData('praisecomment/check', {
        cid: props.comment.id,
        uid: store.state.login.userInfo.mixin_uuid
      })
      if (res.code === 200) {
        ifPraise.value = true
      }
    }
    CheckIfPraise()

    const handlePraiseClick = async () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction')
        return
      }

      if (props.comment.from_uid === store.state.login.userInfo.mixin_uuid) {
        Toast('')
        return
      }

      if (!ifPraise.value) {
        const res = await postPageData(`praisecomment/add`, {
          uid: store.state.login.userInfo.mixin_uuid,
          cid: props.comment.id
        })
        if (res.code === 200) {
          Toast('Praise success!')
          ifPraise.value = true
          emit('handlePraiseClick', props.comment.id, true)
        }
      } else {
        const res = await postPageData(`praisecomment/delete`, {
          cid: props.comment.id,
          uid: store.state.login.userInfo.mixin_uuid
        })
        if (res.code === 200) {
          Toast('Cancel praise!')
          ifPraise.value = false
          emit('handlePraiseClick', props.comment.id, false)
        }
      }
    }

    const commentToUser = () => {
      emit('showDlg', props.comment)
    }
    return {
      ifPraise,
      handlePraiseClick,
      commentToUser
    }
  }
})
