import { Module } from 'vuex'
import { IComment, IMainState, ITopic } from './types'
import { IRootState } from '../types'
import { getPageListData } from '@/service/main/system/system'
import { getCookie } from '@/router/index'

const mainModule: Module<IMainState, IRootState> = {
  namespaced: true,
  state() {
    return {
      topicTrendingList: [],
      topicTrendingCount: 0,
      topicNewList: [],
      topicNewCount: 0,
      topicPoliticsList: [],
      topicPoliticsCount: 0,
      topicSportsList: [],
      topicSportsCount: 0,
      topicCryptoList: [],
      topicCryptoCount: 0,
      topicBusinessList: [],
      topicBusinessCount: 0,

      commentsCount: 0,
      commentsList: [],

      singleTopic: {},
      userCollectTopicCount: 0,
      userCollectTopicList: [],
      userToTopicList: [],
      userToTopicCount: 0
    }
  },
  getters: {
    pageListData(state) {
      return (pageName: string) => {
        return (state as any)[`topic${pageName}List`]
      }
    },
    pageListCount(state) {
      return (pageName: string) => {
        return (state as any)[`topic${pageName}Count`]
      }
    }
  },
  actions: {
    async initMainAction({ commit }) {
      if (getCookie('_betxin_session')) {
        // 1. 
        const userCollectList = await getPageListData(`/collect/list`)
        commit('changeUserCollectTopic', userCollectList.data?.list)
        commit('changeUserCollectTopicCount', userCollectList.data.totalCount)
        // 2. 
        const userToTopicList = await getPageListData(`/usertotopic/list`)
        commit('changeUserToTopic', userToTopicList.data?.list)
        commit('changeUserToTopicCount', userToTopicList.data.totalCount)
      }
    },
    async handleCollecTopictAction({ commit }) {
      const userCollectList = await getPageListData(`/collect/list`)
      commit('changeUserCollectTopic', userCollectList.data?.list)
      commit('changeUserCollectTopicCount', userCollectList.data.totalCount)
    },
    async handleUserToTopicAction({ commit }) {
      const userToTopicList = await getPageListData(`/usertotopic/list`)
      commit('changeUserToTopic', userToTopicList.data?.list)
      commit('changeUserToTopicCount', userToTopicList.data.totalCount)
    },
    async handleClearAllTopicAction({ commit }) {
      commit('changeTopicBusinessList', [])
      commit('changeTopicTrendingList', [])
      commit('changeTopicNewList', [])
      commit('changeTopicPoliticsList', [])
      commit('changeTopicSportsCount', [])
      commit('changeTopicCryptoList', [])
    }
  },
  mutations: {
    changeCommentCount(state, count: number) {
      state.commentsCount = count
    },
    changeCommentList(state, list: IComment[]) {
      state.commentsList = list
    },
    pushCommentList(state, list: IComment[]) {
      state.commentsList.push(...list)
    },
    changeSingleTopic(state, topic: ITopic) {
      state.singleTopic = topic
    },
    changeUserCollectTopic(state, list: ITopic[]) {
      state.userCollectTopicList = list
    },
    changeUserToTopic(state, list: ITopic[]) {
      state.userToTopicList = list
    },
    changeUserCollectTopicCount(state, count: number) {
      state.userCollectTopicCount = count
    },
    changeUserToTopicCount(state, count: number) {
      state.userToTopicCount = count
    },

    pushTopicBusinessList(state, list: any) {
      state.topicBusinessList.push(...list)
    },
    changeTopicBusinessList(state, list: any) {
      state.topicBusinessList = list
    },
    changeTopicBusinessCount(state, count: any) {
      state.topicBusinessCount = count
    },

    pushTopicTrendingList(state, list: any) {
      state.topicTrendingList.push(...list)
    },
    changeTopicTrendingList(state, list: any) {
      state.topicTrendingList = list
    },
    changeTopicTrendingCount(state, count: number) {
      state.topicTrendingCount = count
    },

    pushTopicNewList(state, list: any) {
      state.topicNewList.push(...list)
    },
    changeTopicNewList(state, list: any) {
      state.topicNewList = list
    },
    changeTopicNewCount(state, count: any) {
      state.topicNewCount = count
    },

    pushTopicPoliticsList(state, list: any) {
      state.topicPoliticsList.push(...list)
    },
    changeTopicPoliticsList(state, list: any) {
      state.topicPoliticsList = list
    },
    changeTopicPoliticsCount(state, count: any) {
      state.topicPoliticsCount = count
    },

    pushTopicSportsList(state, list: any) {
      state.topicSportsList.push(...list)
    },
    changeTopicSportsList(state, list: any) {
      state.topicSportsList = list
    },
    changeTopicSportsCount(state, count: any) {
      state.topicSportsCount = count
    },

    pushTopicCryptoList(state, list: any) {
      state.topicCryptoList.push(...list)
    },
    changeTopicCryptoList(state, list: any) {
      state.topicCryptoList = list
    },
    changeTopicCryptoCount(state, count: any) {
      state.topicCryptoCount = count
    }
  }
}

export default mainModule
