import { postPageData } from '@/service/main/system/system'
import { Module } from 'vuex'
import { IRootState } from '../types'
import { ICurrency, IOrderStore } from './types'
import { v4 as uuidv4 } from 'uuid'
import { ClientId } from '@/utils/mixinurl'
import { Toast } from 'vant'

const orderModule: Module<IOrderStore, IRootState> = {
  namespaced: true,
  state() {
    return {
      selectedPrice: 0,
      selectedCoin: {},
      selectedCoinAmount: 0,
      currencyList: []
    }
  },
  getters: {
    getCurrency(state) {
      return (Symbol: string) => {
        console.log(111)
        return state.currencyList.find(
          (item: ICurrency) => item.symbol === Symbol
        )
      }
    }
  },
  actions: {
    async initOrderAction({ state, commit }) {
      const currencyListResult = await postPageData('/currency/list')
      commit('changeCurrencyList', currencyListResult.data.list)
      commit(
        'changeSelectedCoin',
        state.currencyList.find((item: ICurrency) => item.symbol === 'pUSD')
      )
    },
    async handleOrderAction({ state, commit }) {
      const currencyListResult = await postPageData('/currency/list')
      commit('changeCurrencyList', currencyListResult.data.list)
    },
    async handleCreateMixPayAction({ state }, payload: any) {
      console.log(payload)
      const {
        coinAmount: coinAmount,
        yes_ratio: yes_ratio,
        no_ratio: no_ratio,
        tid: tid,
        uid: uid
      } = payload

      const orderId = uuidv4()
      const res = await postPageData('/mixpayorder/add', {
        orderId: orderId,
        PayeeId: ClientId,
        tid: tid,
        yes_ratio: yes_ratio,
        no_ratio: no_ratio,
        uid: uid
      })
      if (res.code === 200) {
        const pUSDCAssetId = '31d2ea9c-95eb-3355-b65b-ba096853bc18'
        //eslint-disable-next-line
        window.location.href = `https://mixpay.me/pay?payeeId=${ClientId}&settlementAssetId=${pUSDCAssetId}&quoteAssetId=${pUSDCAssetId}&quoteAmount=${coinAmount}&orderId=${orderId}&returnTo=${encodeURI('https://betxin.one')}&callbackUrl=${encodeURI('https://betxin.one/api/v1/mixpayorder/update')}`
        // console.log(`https://mixpay.me/pay?payeeId=${ClientId}&settlementAssetId=${pUSDCAssetId}&quoteAssetId=${pUSDCAssetId}&quoteAmount=${coinAmount}&orderId=${orderId}&returnTo=${encodeURI('https://betxin.one')}&callbackUrl=${encodeURI('https://betxin.one/api/v1//mixpayorder/update')}`)
      } else {
        Toast('Fail to pay with mixpay')
      }
    }
  },
  mutations: {
    changeSelectedPrice(state, price: number) {
      state.selectedPrice = price
    },
    changeCurrencyList(state, list: any) {
      state.currencyList = list
    },
    changeSelectedCoin(state, coin: any) {
      state.selectedCoin = coin
    },
    selectedCoinAmount(state, amount: number) {
      state.selectedCoinAmount = amount
    }
  }
}

export default orderModule
