export let ClientId = ''
export let RedirectUrl = ''
export let ReturnUrlPre = ''

if (process.env.NODE_ENV === 'development') {
  ClientId = '30aad5a5-e5f3-4824-9409-c2ff4152724e'
  RedirectUrl = 'http://localhost:3000/oauth/redirect'
  ReturnUrlPre = `http://localhost:8080/topic`
} else if (process.env.NODE_ENV === 'production') {
  ClientId = '7ac9341f-61c1-4b49-b466-298397fe1624'
  RedirectUrl = 'https://betxin.one/oauth/redirect'
  ReturnUrlPre = `https://betxin.one/topic`
} else {
  ClientId = '30aad5a5-e5f3-4824-9409-c2ff4152724e'
  RedirectUrl = 'http://localhost:3000/oauth/redirect'
  ReturnUrlPre = `http://localhost:8080/topic`
}

export const getMixinUrl = (from = '/') => {
  const rootURl = 'https://www.mixin.one/oauth/authorize'

  const options = {
    client_id: ClientId,
    redirect_uri: RedirectUrl,
    scope: 'PROFILE:READ',
    response_type: 'code',
    state: '/'
  }
  const qs = new URLSearchParams(options)
  return `${rootURl}?${qs.toString()}`
}

export const getReturnUrl = (from = '/') => {
  return `${ReturnUrlPre}/${from}`
}
