
import { postPageData } from '@/service/main/system/system'
import { useStore } from '@/store'
import { i18n } from '@/utils/lang'
import { Toast } from 'vant'
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    YesRatioPrice: {
      type: Number,
      default: 0
    },
    NoRatioPrice: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const sellOrderShow = ref(false)
    const store = useStore()
    const yesRefundPrice = ref(0)
    const noRefundPrice = ref(0)
    const overlayShow = ref(false)

    const ClickSellBtnClick = () => {
      sellOrderShow.value = true
    }

    const handleYesPriceClick = () => {
      yesRefundPrice.value = Number(
        (Math.floor(props.YesRatioPrice * 95) / 100).toFixed(2)
      )
    }

    const handleNoPriceClick = () => {
      noRefundPrice.value = Number(
        (Math.floor(props.NoRatioPrice * 95) / 100).toFixed(2)
      )
    }

    const handleRefundClick = async () => {
      if (yesRefundPrice.value === 0 && noRefundPrice.value === 0) {
        Toast(i18n.global.messages[i18n.global.locale].sellOrder.sellTip)
        return
      }
      if (
        yesRefundPrice.value > props.YesRatioPrice * 0.95 ||
        noRefundPrice.value > props.NoRatioPrice * 0.95
      ) {
        Toast(i18n.global.messages[i18n.global.locale].sellOrder.sellTip)
        return
      }
      overlayShow.value = true
      setTimeout(() => {
        overlayShow.value = false
      }, 3000)

      const res = await postPageData('/usertotopic/refund', {
        user_id: store.state.login.userInfo.mixin_uuid,
        tid: store.state.main.singleTopic.tid,
        yes_ratio_price: yesRefundPrice.value,
        no_ratio_price: noRefundPrice.value
      })

      if (res.code === 200) {
        Toast(i18n.global.messages[i18n.global.locale].sellOrder.sellSuccess)
      } else {
        Toast(i18n.global.messages[i18n.global.locale].sellOrder.sellFail)
      }
    }

    return {
      sellOrderShow,
      yesRefundPrice,
      noRefundPrice,
      overlayShow,
      ClickSellBtnClick,
      handleYesPriceClick,
      handleNoPriceClick,
      handleRefundClick
    }
  }
})
