
import { useStore } from '@/store'
import { computed, defineComponent, ref } from 'vue'
import { useRoute } from 'vue-router'
import LixinPopup from '@/base-ui/page-popup/index'
import { popupConfig } from './config/popup.config'
import { imageConfig } from './config/image.config'

export default defineComponent({
  components: {
    LixinPopup
  },
  setup() {
    const route = useRoute()
    const store = useStore()
    const LixinPopupRef = ref<InstanceType<typeof LixinPopup>>()

    const userInfo = computed(() => store.state.login.userInfo)

    const handleLoginClick = () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction', route.path)
      } else {
        LixinPopupRef.value?.handleShowPopup()
      }
    }

    return {
      handleLoginClick,
      userInfo,
      LixinPopupRef,
      popupConfig,
      imageConfig
    }
  }
})
