
import {
  computed,
  ComputedRef,
  defineAsyncComponent,
  defineComponent,
  ref
} from 'vue'
import { useRouter } from 'vue-router'
import PageTopic from '@/components/page-topic/index'
import { ICategory } from '@/service/main/system/types'
import { i18n } from '@/utils/lang'

export default defineComponent({
  components: {
    Business: defineAsyncComponent(() => import('./category/business/index')),
    Crypto: defineAsyncComponent(() => import('./category/crypto/index')),
    New: defineAsyncComponent(() => import('./category/new/index')),
    Politics: defineAsyncComponent(() => import('./category/politics/index')),
    Sports: defineAsyncComponent(() => import('./category/sports/index')),
    Trending: defineAsyncComponent(() => import('./category/trending/index'))
  },
  setup() {
    const router = useRouter()
    const cid = ref()
    const TopicCategory = ref('Business')
    const pageTopicRef = ref<InstanceType<typeof PageTopic>>()
    const categories: ComputedRef<ICategory[]> = computed(
      () => i18n.global.messages[i18n.global.locale].category
    )

    const handleSearchClick = () => {
      router.push('/search')
    }

    const handleTabClick = (item: any) => {
      pageTopicRef.value?.changeFinished()
      const categoryList = [
        { zh: '', en: 'Business' },
        { zh: '', en: 'Crypto' },
        { zh: '', en: 'Sports' },
        { zh: '', en: 'Politics' },
        { zh: '', en: 'New' },
        { zh: '', en: 'Trending' }
      ]
      for (const cate of categoryList) {
        if (item.title === cate.zh) {
          TopicCategory.value = cate.en
          return
        }
      }
      TopicCategory.value = item.title
    }

    return {
      cid,
      categories,
      pageTopicRef,
      handleSearchClick,
      handleTabClick,
      TopicCategory
    }
  }
})
