import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import Main from '@/views/main/main.vue'
import Home from '@/views/main/home/home.vue'
import Topic from '@/views/topic/src/topic.vue'
import store from '@/store'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect: '/main/home'
  },
  {
    path: '/search',
    name: 'search',
    component: () => import('@/views/search/search.vue')
  },
  {
    path: '/topic/:uuid',
    name: 'topic',
    component: Topic
  },
  {
    path: '/main',
    name: 'main',
    component: Main,
    children: [
      {
        path: '/main/home',
        name: 'home',
        component: Home
      },
      {
        path: '/main/user',
        name: 'user',
        component: () => import('@/views/main/user/user.vue')
      },
      {
        path: '/main/feedback',
        name: 'feedback',
        component: () => import('@/views/main/feedback/feedback.vue')
      }
    ]
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'not-found',
    component: () => import('@/views/not-found/not-found.vue')
  }
]
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from) => {
  if (from.fullPath === '/' && getCookie('_betxin_session')) {
    store.dispatch('login/getUserInfoAction')
  }
})

export function getCookie(name: string) {
  const strcookie = document.cookie //cookie
  const arrcookie = strcookie.split('; ') //
  //
  for (let i = 0; i < arrcookie.length; i++) {
    const arr = arrcookie[i].split('=')
    if (arr[0] == name) {
      return true
    }
  }
  return false
}

export default router
