import { Module } from 'vuex'
import { ILoginState } from './types'
import { IRootState } from '../types'
import { getMixinUrl } from '@/utils/mixinurl'
import { getUserInfo } from '@/service/main/user/user'
import { IUserInfo } from '@/service/main/user/types'

const loginModule: Module<ILoginState, IRootState> = {
  namespaced: true,
  state() {
    return {
      userInfo: {}
    }
  },
  getters: {},
  actions: {
    async accountLoginAction({ commit, dispatch }, payload: any) {
      const from = '/'
      window.location.href = getMixinUrl(from)
    },
    async getUserInfoAction({ commit }, payload: any) {
      const userInfo = await getUserInfo('/user/info')
      commit('changeUserInfo', userInfo.data)
    }
  },
  mutations: {
    changeUserInfo(state, userInfo: IUserInfo) {
      state.userInfo = userInfo
    }
  }
}

export default loginModule
