import { App } from 'vue'
import 'vant/lib/index.css'

import {
  Button,
  CellGroup,
  Card,
  Divider,
  Field,
  Grid,
  GridItem,
  Icon,
  Image as VanImage,
  Lazyload,
  Loading,
  Popup,
  Search,
  Tabbar,
  TabbarItem,
  Tag,
  Progress,
  Tabs,
  Tab,
  List,
  ShareSheet,
  Radio,
  RadioGroup,
  Dialog,
  Row,
  Col,
  Popover,
  Overlay
} from 'vant'

const components = [
  Button,
  VanImage,
  Tabbar,
  TabbarItem,
  Field,
  CellGroup,
  Icon,
  Popup,
  Popover,
  Grid,
  GridItem,
  Tag,
  Loading,
  Divider,
  Progress,
  Tabs,
  Tab,
  Card,
  List,
  ShareSheet,
  Radio,
  RadioGroup,
  Dialog,
  Search,
  Row,
  Col,
  Overlay
]
const uses = [Lazyload]

export default function (app: App): void {
  // for (const icon of Icons) {
  //   app.component(icon.name, icon)
  // }
  for (const use of uses) {
    app.use(use)
  }
  for (const component of components) {
    app.component(component.name, component)
  }
}
