
import { computed, defineComponent, ref, watch } from 'vue'
import { useStore } from '@/store'
import { amountInputConfig } from '../config/order.config'
import { Dialog, Notify, Toast } from 'vant'
import { useRoute, useRouter } from 'vue-router'
import { ClientId } from '@/utils/mixinurl'
import { Base64 } from 'js-base64'
import NavOrderLabel from '../cpns/nav-order-label.vue'
import CoinPopup from '../cpns/coin-popup.vue'
import BaseLabel from '@/base-ui/base-label/index'
import { v4 as uuidv4 } from 'uuid'

export default defineComponent({
  components: {
    NavOrderLabel,
    CoinPopup,
    BaseLabel,
    [Dialog.Component.name]: Dialog.Component
  },
  props: {
    orderConfig: {
      type: Object,
      required: true
    }
  },
  setup() {
    const store = useStore()
    const route = useRoute()
    const router = useRouter()
    const showPopup = ref(false)
    const btnDisable = ref(false)
    const NavOrderLabelRef = ref<InstanceType<typeof NavOrderLabel>>()
    const CoinPopupRef = ref<InstanceType<typeof CoinPopup>>()
    const selctedPrice = computed(() => store.state.order.selectedPrice)
    const selectedCoin = computed(() => store.state.order.selectedCoin)
    const coinAmount = computed(() => store.state.order.selectedCoinAmount)
    const selctePriceModel = ref<number>(0)
    const ratio = ref('yes_ratio')

    const handleShowPopup = () => {
      showPopup.value = true
    }
    watch(selctedPrice, (newValue) => {
      selctePriceModel.value = newValue
    })

    watch(selctePriceModel, (newValue) => {
      store.commit('order/changeSelectedPrice', newValue)
      store.commit(
        'order/selectedCoinAmount',
        (selctedPrice.value / selectedCoin.value.price_usd).toFixed(8)
      )
    })

    const handleFieldFocus = () => {
      NavOrderLabelRef.value?.handleBaseLabelZero()
    }

    const handleChooseCoinClick = async () => {
      CoinPopupRef.value?.handleChooseCoinClick()
      await CoinPopupRef.value?.handlerCoinConfig()
    }

    const handleMixinPayClick = () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction', route.path)
        return
      }

      if (ratio.value != 'yes_ratio' && ratio.value != 'no_ratio') {
        Notify({
          message: 'Please Select YES or NO',
          position: 'bottom'
        })
        return
      }
      if (!selctePriceModel.value || selctePriceModel.value === 0) {
        Notify({
          message: 'Please Select Price',
          position: 'bottom'
        })
        return
      }

      if (selctePriceModel.value == 0.0 || isNaN(coinAmount.value)) {
        Toast('Please select value')
        return
      }

      const uuid = uuidv4()
      let yes_ratio = false
      let no_ratio = false
      if (ratio.value === 'yes_ratio') {
        yes_ratio = true
        no_ratio = false
      } else if (ratio.value === 'no_ratio') {
        no_ratio = true
        yes_ratio = false
      }

      const memo = Base64.encode(
        `{"tid": "${store.state.main.singleTopic.tid}", "yes_ratio": ${yes_ratio}, "no_ratio": ${no_ratio}}`
      )
      Dialog.confirm({
        message: 'Have You Paid?',
        confirmButtonColor: '#5979F0',
        confirmButtonText: 'Have paid',
        cancelButtonText: 'No',
        cancelButtonColor: '#AFAFAF'
      })
        .then(() => {
          btnDisable.value = true
          Toast('Please check billing information later')
          store.commit('main/handleClearAllTopicAction')
          setTimeout(() => {
            router.push('/')
          }, 3000)
        })
        .catch(() => {
          Toast('Cancel')
        })
      // console.log(
      //   `mixin://pay?recipient=${ClientId}&asset=${selectedCoin.value.asset_id}&trace=${uuid}&amount=${coinAmount.value}&memo=${memo}`
      // )
      window.location.href = `mixin://pay?recipient=${ClientId}&asset=${selectedCoin.value.asset_id}&trace=${uuid}&amount=${coinAmount.value}&memo=${memo}`
    }

    const handleMixpayClick = () => {
      if (selctePriceModel.value == 0.0 || isNaN(coinAmount.value)) {
        Toast('Please select value')
        return
      }

      let yes_ratio = false
      let no_ratio = false
      if (ratio.value === 'yes_ratio') {
        yes_ratio = true
        no_ratio = false
      } else if (ratio.value === 'no_ratio') {
        no_ratio = true
        yes_ratio = false
      }

      store.dispatch('order/handleCreateMixPayAction', {
        coinAmount: coinAmount.value,
        tid: store.state.main.singleTopic.tid,
        yes_ratio: yes_ratio,
        no_ratio: no_ratio,
        uid: store.state.login.userInfo.mixin_uuid
      })
    }

    return {
      ratio,
      showPopup,
      btnDisable,
      amountInputConfig,
      selctedPrice,
      NavOrderLabelRef,
      CoinPopupRef,
      selctePriceModel,
      selectedCoin,
      coinAmount,
      handleShowPopup,
      handleFieldFocus,
      handleChooseCoinClick,
      handleMixinPayClick,
      handleMixpayClick
    }
  }
})
