
import { IUserInfo } from '@/service/main/user/types'
import { useStore } from '@/store'
import { computed, ComputedRef, defineComponent, ref } from 'vue'
import { imageConfig } from '../config/image.config'
import { buttonConfig } from '../config/button.config'
import { langConfig, langType } from '../config/lang.config'
import { i18n, vantLocales } from '@/utils/lang'

export default defineComponent({
  props: {
    popConfig: {
      type: Object,
      required: true
    }
  },
  setup() {
    const store = useStore()
    const showPopup = ref(false)
    const showLang = ref(false)
    const buyCount = computed(() => store.state.main.userToTopicCount)
    const collectCount = computed(() => store.state.main.userCollectTopicCount)
    const language = computed(() => store.state.lang.language)

    const handleShowPopup = () => {
      showPopup.value = true
    }

    const userInfo: ComputedRef<IUserInfo> = computed(
      () => store.state.login.userInfo
    )

    const changeLangClick = (lang: any) => {
      store.commit('CHANGE_LANGUAGE', lang.value)
      i18n.global.locale = lang.value
      vantLocales(lang.value)
    }

    return {
      language,
      langType,
      showPopup,
      showLang,
      userInfo,
      imageConfig,
      langConfig,
      buttonConfig,
      buyCount,
      collectCount,
      changeLangClick,
      handleShowPopup
    }
  }
})
