
import { getPageListData, postPageData } from '@/service/main/system/system'
import { useStore } from '@/store'
import { IComment } from '@/store/main/types'
import { computed, defineComponent, Ref, ref } from 'vue'
import BaseComponent from './base-component.vue'
import { Dialog, Toast } from 'vant'
import { i18n } from '@/utils/lang'

export default defineComponent({
  components: {
    BaseComponent,
    [Dialog.Component.name]: Dialog.Component
  },
  setup() {
    const store = useStore()
    const topicInfo = computed(() => store.state.main.singleTopic)
    const userInfo = computed(() => store.state.login.userInfo)
    const loading = ref(false)
    const finished = ref(false)
    const commentList: Ref<IComment[]> = ref([])
    const commentCount = ref(0)
    const showCommentup = ref(false)
    const commentText = ref('')
    const DialogTitle = ref(i18n.global.messages[i18n.global.locale].commentTo)
    let ifToUser = false
    const ToUserComment = ref()

    const handleDlgOpen = () => {
      DialogTitle.value = i18n.global.messages[i18n.global.locale].commentTo
      ifToUser = false
      showCommentup.value = true
    }

    const handleDlgClose = () => {
      commentText.value = ''
    }

    const handleCommentClick = async () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction')
        return
      }

      if (ifToUser) {
        // 
        const res = await postPageData(`comment/add`, {
          tid: topicInfo.value.tid,
          content: commentText.value,
          from_uid: userInfo.value.mixin_uuid,
          from_user_name: userInfo.value.full_name,
          from_user_avatar: userInfo.value.avatar_url,
          to_uid: ToUserComment.value.from_uid,
          to_user_name: ToUserComment.value.from_user_name
        })
        setTimeout(async () => {
          if (res.code === 200) {
            Toast(i18n.global.messages[i18n.global.locale].commentSuccess)
            getCommentList()
          } else {
            Toast(i18n.global.messages[i18n.global.locale].commentFailed)
          }
        }, 1000)
      } else {
        // 
        const res = await postPageData(`comment/add`, {
          tid: topicInfo.value.tid,
          content: commentText.value,
          from_uid: userInfo.value.mixin_uuid,
          from_user_name: userInfo.value.full_name,
          from_user_avatar: userInfo.value.avatar_url
        })
        setTimeout(async () => {
          if (res.code === 200) {
            Toast(i18n.global.messages[i18n.global.locale].commentSuccess)
            getCommentList()
          } else {
            Toast(i18n.global.messages[i18n.global.locale].commentFailed)
          }
        }, 1000)
      }
    }

    const getCommentList = () => {
      setTimeout(() => {
        let queryInfo = {
          offset: commentList.value.length,
          limit: 10
        }
        getPageListData(`comment/${topicInfo.value.tid}`, queryInfo).then(
          (res) => {
            if (commentCount.value === 0) {
              commentCount.value = res.data?.totalCount
              commentList.value = res.data?.list
              loading.value = false
              if (commentList.value?.length >= commentCount.value) {
                finished.value = true
              }
            } else if (commentList.value.length < res.data.totalCount) {
              commentCount.value = res.data?.totalCount
              const list: [] = res.data?.list
              commentList.value.push(...list)
              loading.value = false
              if (commentList.value?.length >= commentCount.value) {
                finished.value = true
              }
            } else if (commentList.value.length === commentCount.value) {
              finished.value = true
            }
          }
        )
      }, 1000)
    }

    const showDlg = (comment: any) => {
      ifToUser = true
      DialogTitle.value = `@ ${comment.from_user_name}`
      ToUserComment.value = comment
      showCommentup.value = true
    }

    const handlePraiseClick = (id: number, praise: boolean) => {
      for (const comment of commentList.value) {
        if (comment.id === id) {
          if (praise) {
            comment.praise_num! += 1
          } else {
            comment.praise_num! -= 1
          }
        }
      }
    }

    return {
      loading,
      finished,
      commentCount,
      topicInfo,
      commentList,
      showCommentup,
      commentText,
      DialogTitle,
      getCommentList,
      handleDlgClose,
      handleCommentClick,
      showDlg,
      handlePraiseClick,
      handleDlgOpen
    }
  }
})
