
import { defineComponent } from 'vue'
import BaseLabel from '@/base-ui/base-label/index'
import { OrderConfig } from '../config/order.config'
import { useStore } from '@/store'

export default defineComponent({
  components: { BaseLabel },
  setup() {
    const store = useStore()
    const handleBaseLabelClick = (index: number) => {
      if (OrderConfig.value[index].selcted === true) {
        OrderConfig.value[index].selcted = false
        store.commit('order/changeSelectedPrice')
        return
      } else {
        OrderConfig.value[index].selcted = true
        store.commit(
          'order/changeSelectedPrice',
          OrderConfig.value[index].price
        )
      }

      for (let i = 0; i < OrderConfig.value.length; i++) {
        if (i !== index) {
          OrderConfig.value[i].selcted = false
        }
      }
    }
    const handleBaseLabelZero = () => {
      for (let i = 0; i < OrderConfig.value.length; i++) {
        OrderConfig.value[i].selcted = false
      }
    }

    return { OrderConfig, handleBaseLabelClick, handleBaseLabelZero }
  }
})
