
import { defineComponent, ref, computed } from 'vue'
import { CoinPopUpConfig, CoinConfig } from '../config/coin.config'
import BaseLabel from '@/base-ui/base-label/index'
import { useStore } from '@/store'
import { ICurrency } from '@/store/order/types'

export default defineComponent({
  components: {
    BaseLabel
  },
  setup() {
    const showCoinPopup = ref(false)
    const store = useStore()
    const selctedPrice = computed(() => store.state.order.selectedPrice)
    const selectedCoin = computed(() => store.state.order.selectedCoin)
    const handlerCoinConfig = () => {
      CoinConfig.value = []
      store.state.order.currencyList.forEach((item: ICurrency) => {
        CoinConfig.value.push({
          selected: false,
          asset_id: item.asset_id,
          price_btc: item.price_btc,
          price_usd: item.price_usd,
          chain_id: item.chain_id,
          icon_url: item.icon_url,
          symbol: item.symbol
        })
      })
    }

    handlerCoinConfig()

    const handleChooseCoinClick = () => {
      showCoinPopup.value = true
    }

    const handleCoinLabelClick = (index: number) => {
      CoinConfig.value[index].selected = true
      store.commit('order/changeSelectedCoin', CoinConfig.value[index])
      showCoinPopup.value = false
      store.commit(
        'order/selectedCoinAmount',
        (selctedPrice.value / selectedCoin.value.price_usd).toFixed(8)
      )
    }

    return {
      CoinPopUpConfig,
      CoinConfig,
      showCoinPopup,
      handleChooseCoinClick,
      handlerCoinConfig,
      handleCoinLabelClick
    }
  }
})
